@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap");
.button-assessment {
  color: #fff;
  border-radius: 1rem;
  padding: 1rem 2rem;
  width: 22rem;
  text-decoration: none;
  justify-content: space-between !important;
  align-items: center !important;
  display: flex;
  cursor: pointer;
  font-size: 1.8rem;
  background-color: #000;
  transition: all 0.2s !important;
}
@media (max-width: 56.25em) {
  .button-assessment {
    width: calc(100% - 4rem);
    max-width: 22rem !important;
  }
}
.button-assessment p {
  margin: 0;
}
.button-assessment:hover {
  transform: scale(1.02);
}
.button-large {
  color: #fff;
  border-radius: 1rem;
  padding: 1rem;
  width: 100%;
  cursor: pointer;
  font-size: 1.8rem;
  background-color: #000;
  transition: all 0.2s !important;
}
.button-large p {
  display: inline;
}
.button-sm {
  color: #fff;
  border-radius: 1rem;
  padding: 1rem;
  display: block;
  margin: 6rem auto 3rem;
  width: 20rem;
  cursor: pointer;
  font-size: 1.8rem;
  background-color: #000;
  transition: all 0.2s !important;
}
.button-sm p {
  display: inline;
}
.button-sm:hover {
  transform: scale(1.02);
}

.input {
  border: 1px solid #AEAEAE;
  background-color: #F9F9F9;
  font-size: 1.6rem;
  border-radius: 1rem;
  width: 100%;
  padding: 1rem;
  height: 3rem;
}
.input-hidden {
  display: none;
}
.input:focus {
  outline: none;
}

.sidebar {
  width: 5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  flex-direction: column;
  height: calc(100vh - 4rem);
  padding: 2rem 1rem;
}
.sidebar-each {
  position: relative;
  width: 100%;
  text-align: center;
}
.sidebar-each:hover .sidebar-each-toolkit {
  opacity: 1;
  transform: translate(5rem, 0%);
}
.sidebar-each-toolkit {
  transition: opacity 0.2s;
  position: absolute;
  left: 0;
  top: 0;
  transform: translate(-200%, 0%);
  z-index: 4;
  opacity: 0;
  padding: 1rem 1.5rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  background-color: #fff;
  border-radius: 1rem;
  width: 10rem;
}
.sidebar-each-toolkit-each {
  display: flex !important;
  justify-content: space-between !important;
  align-items: center !important;
  align-items: center !important;
  text-decoration: none !important;
  width: 100%;
  font-size: 1.4rem !important;
  text-align: left;
  transition: all 0.2s;
  font-weight: 400;
  padding: 1rem 0.1rem;
  margin: auto;
  color: rgba(0, 0, 0, 0.7);
}
.sidebar-each-toolkit-each-image {
  border-radius: 100%;
  width: 2.4rem;
  font-size: 2rem !important;
  object-fit: cover;
}
.sidebar-each-toolkit-each p {
  margin: 0 1rem 0;
}
.sidebar-each-toolkit-each:not(:last-child) {
  border-bottom: 0.5px solid #AEAEAE;
}
.sidebar-each-toolkit-each:hover {
  font-size: 1.45rem !important;
}
.sidebar-icon {
  margin: 1.5rem auto;
  cursor: pointer;
  transition: all 0.1s;
  font-size: 2.5rem !important;
  color: #B8B8B8;
}
.sidebar-icon-selected {
  filter: brightness(0.1) sepia(0.2) hue-rotate(0deg) saturate(0);
}
.sidebar-icon-active {
  filter: brightness(0.1) sepia(0.2) hue-rotate(0deg) saturate(0);
}
.sidebar-icon:hover {
  transform: scale(1.2);
  filter: brightness(0.1) sepia(0.2) hue-rotate(0deg) saturate(0);
}

.auth {
  max-width: 50rem;
  margin: 0 auto;
  padding: 6rem 0;
  overflow: auto;
  min-height: calc(100vh - 12rem);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  color: rgba(0, 0, 0, 0.8);
}
.auth-2 {
  justify-content: flex-start;
}
.auth-back {
  display: flex;
  align-items: center;
  justify-content: space-between;
  align-self: start !important;
  text-decoration: none !important;
}
.auth-back-text {
  margin-left: 0.5rem;
  font-weight: 600;
  transition: all 0.2s;
  color: rgba(0, 0, 0, 0.8);
  font-size: 1.8rem;
}
.auth-back-text:hover {
  color: black;
}
.auth-header {
  font-size: 4rem;
  color: rgba(0, 0, 0, 0.8);
}
.auth-redirect {
  font-size: 1.8rem;
  font-weight: lighter;
  color: rgba(0, 0, 0, 0.6);
  font-weight: 600;
}
.auth-redirect-link {
  transition: all 0.2s;
  color: rgba(0, 0, 0, 0.8);
  text-decoration: none;
}
.auth-redirect-link:hover {
  color: black;
}
.auth-google {
  width: 9rem;
  padding: 2rem;
  margin: 2rem auto !important;
}
.auth-google-icon {
  cursor: pointer;
  transition: all 0.2s !important;
  font-size: 4rem !important;
}
.auth-google-icon:hover {
  color: #000000;
  transform: scale(1.1);
}
.auth-google-or {
  font-size: 2rem;
  margin: auto auto 1rem;
}
.auth-form {
  text-align: left;
  display: flex;
  width: 100%;
  flex-direction: column;
}
.auth-form-alert {
  align-self: end;
  color: #FF0000;
  font-size: 1.6rem;
}
.auth-form-user {
  margin: 0 auto 3rem;
  text-align: center;
}
.auth-form-user p {
  font-size: 2rem;
  font-weight: 600;
  margin: 2rem auto;
}
.auth-form-user img {
  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.5);
  border-radius: 100%;
  width: 15rem;
}
.auth-form-title-2 {
  font-size: 2rem;
  font-weight: 600;
  margin: 0rem auto 4rem;
}
.auth-form-title {
  font-size: 2rem;
  font-weight: 700;
}
.auth-form-content {
  display: flex;
  width: 100%;
  flex-direction: column;
}
.auth-form-forgot {
  margin: 1.5rem 0 4rem !important;
  font-size: 1.6rem;
  text-decoration: none;
  color: rgba(0, 0, 0, 0.8) !important;
  cursor: pointer;
  transition: all 0.2s;
}
.auth-form-forgot:hover {
  color: #000 !important;
}
.auth-form-input {
  border: 1px solid #AEAEAE;
  background-color: #F9F9F9;
  font-size: 1.6rem;
  padding: 1rem;
  height: 3rem;
}
.auth-form-input-hidden {
  display: none;
}
.auth-form-input-profile {
  position: relative;
  border: 1px solid #AEAEAE;
  border-radius: 1rem;
  margin: 2rem auto;
  width: 20rem;
  cursor: pointer;
  background-color: #F9F9F9;
  transition: all 0.2s;
  min-height: 8rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 1rem;
}
.auth-form-input-profile-cross {
  position: absolute;
  top: 2rem;
  background-color: #fff;
  padding: 0.2rem;
  height: 2rem;
  border-radius: 100%;
  display: flex;
  transition: all 0.2s;
  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.5);
  justify-content: center;
  align-items: center;
  width: 2rem;
  right: 3rem;
}
.auth-form-input-profile-cross:hover {
  transform: scale(1.1);
}
.auth-form-input-profile-cross-icon {
  font-size: 2rem !important;
}
.auth-form-input-profile-image {
  width: 18rem;
  height: 18rem;
  object-fit: cover;
  border-radius: 1rem;
  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.5);
}
.auth-form-input-profile-icon {
  font-size: 2rem !important;
}
.auth-form-input-profile p {
  font-size: 1.4rem;
  margin: 0 auto 1.5rem;
}
.auth-form-input-profile:hover {
  background-color: #fff;
}
.auth-form-input:focus {
  outline: none;
}
.auth-form-input:not(:last-child) {
  border-bottom: none;
}
.auth-form-input:nth-child(1) {
  border-top-right-radius: 1rem;
  border-top-left-radius: 1rem;
}
.auth-form-input:last-child {
  border-bottom-right-radius: 1rem;
  border-bottom-left-radius: 1rem;
}

