.not-found {
  width: calc(100% - 6rem);
  padding: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: calc(100vh - 6rem);
}
.not-found h1 {
  font-size: 10rem;
  margin: 0.4rem;
}
.not-found p {
  font-size: 2rem;
}
.not-found Link,
.not-found a {
  color: #000000 !important;
  font-size: 2rem;
  transition: all 0.2s;
  cursor: pointer;
  position: relative;
  text-decoration: none;
}
.not-found Link::after,
.not-found a::after {
  width: 100%;
  height: 0.75rem;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #000000;
  content: "";
  transform: translate(-0.5rem, 0);
  padding: 0 0.5rem;
  transition: all 0.2s;
}
.not-found Link:hover,
.not-found a:hover {
  color: #fff !important;
  transform: scale(1.1);
}
.not-found Link:hover::after,
.not-found a:hover::after {
  height: 100%;
  top: 0%;
  border-radius: 0.5rem;
  padding: 0.5rem;
  transform: translate(-0.5rem, -0.3rem);
  z-index: -1;
}

